                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right"  data-aos-duration="900">
                                    <b-col cols="3">
                                        <p><strong>Religion:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{religion}}</p>
                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right"  data-aos-duration="1000">
                                    <b-col cols="3">
                                        <p><strong>Status:</strong></p>
                                    </b-col>
