                </b-row>
               <b-row class="p-3 mt-1"  v-for="(e) in education" :key="e.level">
                    <b-col >
                        <b-row>
                            <p class="font-weight-bold" >{{capital(e.level)}}</p>
                        </b-row>
                        <b-row>
                            <b-col cols="5">
                                <img :src=getPic(e.imageUrl) style="max-width: 150px; height:auto" />
                            </b-col>
                            <b-col cols="7">
                                <p class="font-weight-bold h4">{{e.name}}</p>
                                <p>{{e.address}}</p>
                                <p style="margin-top: -15px;" v-html="e.details"></p>
                                <p style="margin-top: -15px;">{{e.yearAttended}}</p>
                            </b-col>
                        </b-row>
                    </b-col>
               </b-row>
            </b-col>
        </b-row>

    </b-container>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'Myself',
  data(){
    return {
        'birthDate' : '10/08/1998',
        'age' : 0,
        'fullName' : 'Keith Andrew S. Pacio',
        'birthDateStr' : 'October 08, 1998',
        'gender' : 'Male',
        'religion' : 'Roman Catholic',
        'maritalStatus' : 'Single',
        'education' : [
            {
                'imageUrl' : 'assets/sample-pic.jpg',
                'level' : 'preschool',
                'name' : 'Saniboy Day Care Center',
                'address' : 'Saniboy Hulo, Mandaluyong',
                'details' : '',
                'yearAttended' : '2002-2003'
            },
            {
                'imageUrl' : 'assets/sample-pic.jpg',
                'level' : 'kinder',
                'name' : 'Saniboy Day Care Center',
                'address' : 'Saniboy Hulo, Mandaluyong',
                'details' : '',
                'yearAttended' : '2003-2004'
            },
            {
                'imageUrl' : 'assets/sample-pic.jpg',
                'level' : 'primary',
                'name' : 'Mandaluyong Elementary School',
                'address' : 'Rev. G. Aglipay St., Poblacion, Mandaluyong',
                'details' : '',
                'yearAttended' : '2004-2010'
            },
            {
                'imageUrl' : 'assets/sample-pic.jpg',
                'level' : 'secondary',
                'name' : 'Isaac Lopez Integrated School',
                'address' : 'Ilino Cruz Street, Vergara, Mandaluyong City',
                'details' : '<p class="font-weight-bold">7th Honorable Mentioned, Best in Mathematics, Best in Science</p>',
                'yearAttended' : '2010-2014'
            },
            {
                'imageUrl' : 'assets/sample-pic.jpg',
                'level' : 'tertiary',
                'name' : 'Polytechnic University of the Philippines',
                'address' : 'Anonas, Sta. Mesa, Manila City',
                'details' : '<p class="font-weight-bold">Bachelor of Science in Computer Engineering</p>',
                'yearAttended' : '2014-2019'
            },
        ]
    }
  },
  mounted(){
    this.getAge();   
  },
  methods:{
     getAge : function(){ //get current age
        var today = new Date();
        var birthDate = new Date(this.birthDate);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age--;
        }
        this.age = age;
     }, 
     capital : function(str = ''){
        return str.toString().toUpperCase();
     },
     getPic (name) {
      return require("@/" + name);
     }

  },
  computed:{
     
  },
  watch: {
      
  }
}
</script>

